/* INDEX HTML */
/*HEADER*/

.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #006673;
    padding: 17px 60px;
}

.nav__ul {
    display: flex;
}

.nav__li {
    margin-right: 50px;
}

.nav__li a {
    color: #e3e3e3;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    position: relative;
    transition: all .3s;
}

.nav__li a::after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #E6CE4F;
    position: absolute;
    left: 0;
    bottom: -33px;
    display: none;
}

.nav__li a:hover {
    color: #fff;
    transition: all .3s;
}

.nav__li a:focus {
    color: #fff;
    transition: all .3s;
}

.nav__li a:hover:after {
    display: block;
    transition: all .3s;
}

.nav__li a:focus:after {
    display: block;
    transition: all .3s;
}

.nav__li:last-child {
    margin-right: -150px;
}

.instr-btn {
    display: inline-flex !important;
    min-width: 180px;
    border: 2px solid #E6CE4F;
    border-radius: 100px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 150%;
    align-items: center;
    text-align: right;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: #FFFFFF;
    padding: 12px 38px 12px 11px;

    img {
        margin-right: 8px;
    }
}

.instr-btn_yellow {
    color: #252E2F;
    background-color: #E6CE4F;
}

.hamburger-menu {
    display: none;
}

#menu__toggle {
    opacity: 0;
}

.menu__btn {
    display: none;
    align-items: center;
    position: fixed;
    top: 20px;
    right: 30px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 6;
}

.menu__btn > span,
.menu__btn > span::before,
.menu__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
}

.menu__btn > span::before {
    content: '';
    top: -8px;
}

.menu__btn > span::after {
    content: '';
    top: 8px;
}

.menu__box {
    display: block;
    position: fixed;
    visibility: hidden;
    top: 0;
    right: -100%;
    width: 300px;
    height: 100%;
    margin: 0;
    padding: 80px 0;
    list-style: none;
    text-align: center;
    background-color: #006673;
    box-shadow: 1px 0px 6px rgba(0, 0, 0, .2);
    z-index: 5;
}

.menu__item {
    display: block;
    padding: 12px 24px;
    color: #333;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
}

#menu__toggle:checked ~ .menu__btn > span {
    transform: rotate(45deg);
}

#menu__toggle:checked ~ .menu__btn > span::before {
    top: 0;
    transform: rotate(0);
}

#menu__toggle:checked ~ .menu__btn > span::after {
    top: 0;
    transform: rotate(90deg);
}

#menu__toggle:checked ~ .menu__box {
    visibility: visible;
    right: 0;
}

.menu__btn > span,
.menu__btn > span::before,
.menu__btn > span::after {
    transition-duration: .25s;
}

.menu__box {
    transition-duration: .25s;
}

.menu__item {
    transition-duration: .25s;
}

